@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
.container {
  margin: 0 auto;
  padding: 0 30px;
  min-width: 320px;
  max-width: 1150px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}
.center {
  display: flex;
  justify-content: center;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  font-size: 1px;
}
.button {
  background-color: #d9e8ef;
  border: 2px solid #444444;
  box-sizing: border-box;
  color: #444444;
  height: 52px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 17px;
  line-height: 20px;
  display: block;
  cursor: pointer;
  font-family: "Open Sans", "Arial", sans-serif;
}
.button:hover {
  background-color: #444444;
  color: #d9e8ef;
}
.button:active {
  background-color: #444444;
  color: #ffffff4d;
}
.page {
  height: 100%;
}
.page__body {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  background-color: #0d76a0;
  color: #002366;
  font-size: 15px;
  line-height: 24px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  min-width: 320px;
  width: 1050px;
  position: relative;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.5);
}
@media (min-width: 768px) {
  .page__body::after {
    content: "";
    position: absolute;
    left: 0;
    top: 88px;
    width: 100%;
    border-bottom: 1px solid #0000001a;
  }
}
.page__main {
  flex-grow: 1;
  background-color: #d9e8ef;
  padding-top: 88px;
}
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #ffffffe6;
  z-index: 3;
  display: none;
}
.modal__content {
  background-color: #d9e8ef;
  opacity: 100%;
  min-height: 336px;
  width: 100%;
  max-width: 566px;
  position: relative;
  margin: 0 auto;
  align-self: center;
  padding: 20px 30px 46px;
  box-sizing: border-box;
  border: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
}
.modal__title {
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
  text-transform: uppercase;
  text-align: center;
}
.modal__fieldset {
  border: none;
  padding: 0;
  text-align: center;
}
.modal__legend {
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 30px;
  color: #444444;
}
.modal__list {
  list-style: none;
  margin: 0;
  justify-content: center;
  padding: 0;
  padding-top: 20px;
  padding-bottom: 31px;
  display: grid;
  grid-template-columns: min-content min-content min-content;
  column-gap: 7px;
}
.modal__radio input {
  display: contents;
}
.modal__radio input:checked ~ .modal__text {
  border-color: #0d76a0;
}
.modal__text {
  font-style: normal;
  font-weight: bold;
  font-size: 17px;
  line-height: 16px;
  color: #444444;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 82px;
  height: 52px;
  border: 2px solid #4444441a;
  cursor: pointer;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .modal__text {
    width: 112px;
  }
}
.modal__button {
  width: 260px;
  height: 52px;
  background-color: #0d76a0;
  border: none;
  color: #d9e8ef;
  text-transform: uppercase;
  font-size: 17px;
  font-weight: bold;
}
@media (min-width: 768px) {
  .modal__button {
    width: 350px;
  }
}
.header {
  min-width: 320px;
}
@media (min-width: 768px) {
  .header {
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto;
    max-width: 1150px;
    min-width: 668px;
    background-color: #d9e8ef;
  }
}
.main-navigation {
  background-color: #d9e8ef;
  width: 100%;
}
@media (min-width: 768px) {
  .main-navigation {
    position: unset;
    grid-column: 1 / -1;
    grid-row: 1 / -1;
    background-color: transparent;
    min-width: 672px;
  }
}
@media (min-width: 1150px) {
  .main-navigation {
    min-width: 1050px;
  }
}
.main-navigation__list {
  list-style: none;
  padding: 0;
  margin: 0;
  grid-auto-flow: column;
  z-index: 1;
}
@media (min-width: 768px) {
  .main-navigation__list {
    display: grid;
    grid-template-columns: max-content;
    width: 100%;
    align-items: center;
  }
}
@media (min-width: 1150px) {
  .main-navigation__list {
    width: 1050px;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    position: fixed;
    background-color: #d9e8ef;
  }
}
.main-navigation__item {
  display: flex;
  height: auto;
}
.main-navigation__item::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  border-bottom: 1px solid #ececec;
}
@media (min-width: 768px) {
  .main-navigation__item::after {
    content: none;
  }
}
.main-navigation__item--active {
  opacity: 0.6;
  text-decoration: underline;
}
.main-navigation__link {
  color: #002366;
  text-decoration: none;
  font-weight: bold;
  font-size: 18px;
  line-height: 150%;
  min-height: 88px;
  box-sizing: border-box;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 28px;
}
.main-navigation__link:hover {
  background-color: #4169e154;
}
.main-navigation__link:active {
  opacity: 0.3;
}
.main-navigation__logo {
  display: flex;
  justify-content: center;
}
.main-navigation__logo-image {
  width: 88px;
  height: auto;
  display: flex;
}
.empty-cart {
  font-family: "Open Sans", "Arial", sans-serif;
}
@media (min-width: 768px) {
