.custom-radio {
  cursor: pointer;
}
.custom-radio > input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.custom-radio > span {
  display: inline-flex;
  align-items: center;
  user-select: none;
}
.custom-radio > span::before {
  content: "";
  display: inline-block;
  width: 35px;
  height: 35px;
  flex-shrink: 0;
  flex-grow: 0;
  background-color: transparent;
  border-radius: 50%;
  margin-right: 0.5em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
  border: 2px solid #444444;
}
@media (min-width: 768px) {
  .custom-radio > span::before {
    width: 45px;
    height: 45px;
  }
}
.custom-radio > input + span:hover::before,
.custom-radio__input:focus ~ span::before {
  border-color: #0d76a0;
}
.custom-radio > input + span:active::before {
  border-color: #4444444d;
}
.custom-radio > input:checked + span::before {
  background-color: transparent;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 26 26'%3e%3ccircle cx='13' cy='13' r='13' fill='%2363d1bb'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: center center;
}
.custom-radio__text {
  color: #002366;
  font-weight: bold;
  font-size: 17px;
  line-height: 18px;
}
@media (min-width: 768px) {
  .custom-radio__text {
    font-size: 19px;
  }
}
.custom-radio__note {
  padding-left: 11px;
  padding-top: 5px;
}
@media (min-width: 768px) {
  .custom-radio__note {
    padding-left: 17px;
    padding-top: 0;
    margin-top: -4px;
  }
}
@media (min-width: 1150px) {
  .custom-radio__note {
    padding-left: 20px;
  }
}
.custom-checkbox {
  cursor: pointer;
}
.custom-checkbox > input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.custom-checkbox > span {
  display: inline-flex;
  align-items: center;
  user-select: none;
}
.custom-checkbox > span::before {
  content: "";
  display: inline-block;
  width: 34px;
  height: 34px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 2px #444444 solid;
  margin-right: 0.5em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
  background-color: transparent;
}
@media (min-width: 768px) {
  .custom-checkbox > span::before {
    width: 52px;
    height: 52px;
    box-sizing: border-box;
  }
}
.custom-checkbox > input:not(:disabled) + span:hover::before,
.custom-checkbox__input:focus ~ span::before {
  border-color: #0d76a0;
  background-color: transparent;
}
.custom-checkbox > input:not(:disabled) + span:active::before {
  border-color: #4444444d;
}
.custom-checkbox > input:checked + span::before {
  background: transparent;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 17'%3e%3cpath fill='%23000000' d='M23.963 1.68385L22.9687 0.692322L8.76555 14.7862L1.73497 7.77467L0.740742 8.76619L7.77132 15.7777L8.76555 16.7692L9.83079 15.7777L23.963 1.68385Z'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 25px;
}
@media (min-width: 768px) {
  .custom-checkbox > input:checked + span::before {
    background-size: 33px;
  }
}
.custom-checkbox > input:disabled + span::before {
  background-color: rgba(255, 255, 255, 0.3);
}
.custom-checkbox__text {
  color: #002366;
  font-weight: bold;
  font-size: 17px;
  line-height: 18px;
  padding-bottom: 17px;
}
@media (min-width: 768px) {
  .custom-checkbox__text {
    font-size: 19px;
  }
}
